import React from "react";
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import { Tooltip } from '@material-ui/core';
import { Card } from '@material-ui/core';
import { Link } from '@material-ui/core';
import { CardHeader } from '@material-ui/core';
import { CardMedia } from '@material-ui/core';
import { CardContent } from '@material-ui/core';
import { CardActions } from '@material-ui/core';
import { Collapse } from '@material-ui/core';
import { Avatar } from '@material-ui/core';
import { IconButton } from '@material-ui/core';
import { Typography } from '@material-ui/core';
import { red } from '@material-ui/core/colors';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import TelegramIcon from '@material-ui/icons/Telegram';
import CommentIcon from '@material-ui/icons/Comment';
import WhatsAppIcon from '@material-ui/icons/WhatsApp';
import FacebookIcon from '@material-ui/icons/Facebook';
import TwitterIcon from '@material-ui/icons/Twitter';
import { ConvertDate, Linkify } from "../utils/CommonMethods";
import { siteOwner, blogImageBaseUrl, blogBaseRelativeUrl, ShareBlogUrl } from "../configs/SiteConfig";
import  BlogMenu  from "./BlogMenu";
import {LinkPath} from './Wrappers';

const useStyles = makeStyles((theme) => ({
    root: {
        maxWidth: 500,
        margin: 20
    },
    msgCard: {
        margin: 20,
        padding: 20
    },
    media: {
        height: 0,
        paddingTop: '56.25%', // 16:9
    },
    expand: {
        transform: 'rotate(0deg)',
        marginLeft: 'auto',
        transition: theme.transitions.create('transform', {
            duration: theme.transitions.duration.shortest,
        }),
    },
    expandOpen: {
        transform: 'rotate(180deg)',
    },
    avatar: {
        backgroundColor: red[500],
    },
    link:{
        textDecoration: 'none',
        color: theme.palette.text.secondary
    }
}));

export default function BlogDisplay(props, replaceHeadComponents) {
    const classes = useStyles();
    const [expanded, setExpanded] = React.useState(false);
    const [openImage, setOpenImage] = React.useState(false);

    const handleExpandClick = () => {
        setExpanded(!expanded);
    };

    const onImageClick = () => {
        setOpenImage(true);
    };
    
    return (
        <Card elevation={3} className={classes.root}>
            <CardHeader
                avatar={
                    <Avatar aria-label="recipe" className={classes.avatar} src={siteOwner.avatarImageUrl} alt={siteOwner.name}>
                    </Avatar>
                }
                action={
                    <BlogMenu blogId={props.blog.rowKey}></BlogMenu>                    
                }

                title={props.blog.title}
                subheader={ <LinkPath className={classes.link} to={blogBaseRelativeUrl + props.blog.rowKey}>{ConvertDate(props.blog.created)}</LinkPath>}
            />
            { (props.blog.imageUrl === undefined || props.blog.imageUrl === null || props.blog.imageUrl === '') ?
                <div></div> :
                (<CardMedia
                    className={classes.media}
                    image={blogImageBaseUrl + props.blog.imageUrl}
                    title={props.blog.title}
                    onClick={onImageClick}
                />)
            }

            <CardContent>
                {
                    <Typography variant="body2" color="textSecondary" dangerouslySetInnerHTML={{__html: Linkify(props.blog.content)}} />                                      
                }
            </CardContent>
            <CardActions disableSpacing>
                <Tooltip title="Message Author on Telegram">
                    <Link href={ShareBlogUrl.TelegramChat} target="_blank">
                        <IconButton aria-label="comment">
                            <CommentIcon />
                        </IconButton>
                    </Link>
                </Tooltip>
                <Tooltip title="Share on WhatsApp">
                    <Link href={ShareBlogUrl.WhatsApp + props.blog.rowKey} target="_blank">
                        <IconButton aria-label="comment">
                            <WhatsAppIcon />
                        </IconButton>
                    </Link>
                </Tooltip>
                <Tooltip title="Share on Telegram">
                    <Link href={ShareBlogUrl.Telegram + props.blog.rowKey} target="_blank">
                        <IconButton aria-label="comment">
                            <TelegramIcon />
                        </IconButton>
                    </Link>
                </Tooltip>
                <Tooltip title="Share on Facebook">
                    <Link href={ShareBlogUrl.Facebook + props.blog.rowKey} target="_blank">
                        <IconButton aria-label="comment">
                            <FacebookIcon />
                        </IconButton>
                    </Link>
                </Tooltip>
                <Tooltip title="Share on Twitter">
                    <Link href={ShareBlogUrl.Twitter + props.blog.rowKey} target="_blank">
                        <IconButton aria-label="comment">
                            <TwitterIcon />
                        </IconButton>
                    </Link>
                </Tooltip>

                <Tooltip title="Read More">
                    <IconButton
                        className={clsx(classes.expand, {
                            [classes.expandOpen]: expanded,
                        })}
                        onClick={handleExpandClick}
                        aria-expanded={expanded}
                        aria-label="Read more"
                    >
                        <ExpandMoreIcon />
                    </IconButton>
                </Tooltip>
            </CardActions>
            <Collapse in={expanded} timeout="auto" unmountOnExit>
                <CardContent>
                    {
                        <Typography variant="body2" color="textSecondary" paragraph dangerouslySetInnerHTML={{__html: Linkify(props.blog.extendedContent)}} />
                    }
                </CardContent>
            </Collapse>
        </Card>
    );
}